import {
  Directive, HostBinding, Inject, Input, OnInit, OnDestroy, ElementRef, Renderer2
} from '@angular/core';

import { AppDropdownDirective } from './dropdown.directive';
import * as $ from 'jquery';
import { BehaviorSubject } from 'rxjs';
import { LayoutService } from '../services/layout.service';

@Directive({
  selector: '[appDropdownLink]'
})
export class DropdownLinkDirective {
 
  
  @Input() public group: any;
  
    @HostBinding('class.activeSMenu')
    @Input()
    get open(): boolean {
      return this._open;
    }
  
    set open(value: boolean) {
      this._open = value;
      const hostElem = this.el.nativeElement;
      if (value) {
        this.nav.closeOtherLinks(this);
        $(hostElem).find('ul.subMenuCol').slideDown();
      }
      let part = this.el.nativeElement.querySelector('ul.subMenuCol');
      this.renderer.setStyle(part, 'display', 'none');
    }
  
    protected _open: boolean;
    protected nav:any;
  
    public constructor(nav: AppDropdownDirective,
                      private el: ElementRef,
                      private renderer: Renderer2,
                      private layoutService:LayoutService) {
      this.nav = nav;
    }
  
    public ngOnInit(): any {
      this.nav.addLink(this);

     
     
    }
  
    public ngOnDestroy(): any {
      this.nav.removeGroup(this);
    }
  
    public toggle(): any {
      this.open = !this.open;
      let part = this.el.nativeElement.querySelector('ul.subMenuCol');
      this.layoutService.content.next(this.el.nativeElement);
      const hostElem = this.el.nativeElement;
      if(this.open){
        this.renderer.setStyle(part, 'display', 'block');
        $(hostElem).find('ul.subMenuCol').slideDown();
      }else{
        this.renderer.setStyle(part, 'display', 'none');
        $(hostElem).find('ul.subMenuCol').slideUp();
      }

     
    }

}
