import { DOCUMENT } from '@angular/common';
import { Directive, HostBinding, Inject, Input, OnInit, OnDestroy, HostListener } from '@angular/core';

@Directive({
    selector: '[menuCollapse]',
})
export class MenuCollapseDirective {
    expanded: boolean = false;

    constructor(@Inject(DOCUMENT) private document: Document) { }

    @HostListener('click', ['$event'])
    onClick(e: any) {
        
        this.toggleCollapse();
    }

    toggleCollapse() {
        this.expanded = !this.expanded;
        if (this.expanded) {
            
            this.document.body.classList.add('activeNav');
        } else {
           
            this.document.body.classList.remove('activeNav');
        }
    }

}
