import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { Router, ActivatedRoute } from "@angular/router";
import { map, catchError, delay } from "rxjs/operators";
import { of, BehaviorSubject, throwError, Observable } from "rxjs";
import { LocalStoreService } from "./local-store.service";

export const DEMO_TOKEN =
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1c2VyTmFtZSI6IlBSQUJIQVQiLCJ1c2VySWQiOjEsInRlbGxlck5hbWUiOiJQUkFCSEFUIEsifQ.QwgZI9XZtYS6OUJLL1vlEHBi6KLCSgv7O10Kqi-uY_4";

export const DEMO_USER: any = { 
  "userName": "PRABHAT", 
  "userId": 1, 
  "tellerName": "PRABHAT K" 
};

@Injectable({
  providedIn: "root",
})
export class JwtAuthService {
  token;
  isAuthenticated: boolean;
  user: any = {};
  user$ = (new BehaviorSubject<any>(this.user));
  signingIn: boolean;
  return: string;
  JWT_TOKEN = "token";
  APP_USER = "ICUST_USER";

  constructor(
    private ls: LocalStoreService,
    private http: HttpClient,
    private router: Router,
    private route: ActivatedRoute
  ) {
   
  }

  /**
 * Setter & getter for access token
 */
  set accessToken(token: string) {
    localStorage.setItem('accessToken', token);
  }

  get accessToken(): string {
    return localStorage.getItem('accessToken') ?? '';
  }

  public signin() {
    return of({ token: DEMO_TOKEN, user: DEMO_USER })
      .pipe(
        delay(1000),
        map((res: any) => {
          this.setUserAndToken(res.token, res.user, !!res);
          this.signingIn = true;
          return res;
        }),
        catchError((error) => {
          return throwError(error);
        })
      );

  }


  /*
    checkTokenIsValid is called inside constructor of
    shared/components/layouts/admin-layout/admin-layout.component.ts
  */
  public checkTokenIsValid() {
    return of(DEMO_USER)
      .pipe(
        map((profile: any) => {
          this.setUserAndToken(this.getJwtToken(), profile, true);
          this.signingIn = false;
          return profile;
        }),
        catchError((error) => {
          return of(error);
        })
      );

  }

  public signout() {
    this.ls.clear();
    sessionStorage.clear();
    localStorage.removeItem('userFromLogin');
    localStorage.clear();
    this.signingIn = false;
    this.router.navigate(["/login"]);
  }

  isLoggedIn(): boolean {
    return !!this.getJwtToken();
  }

  getJwtToken() {
    return this.ls.getItem(this.JWT_TOKEN);
  }
  getUser() {
    return this.ls.getItem(this.APP_USER);
  }

  setUserAndToken(token?: string, user?: any, isAuthenticated?: boolean) {
    this.isAuthenticated = isAuthenticated;
    this.token = token;
    this.accessToken = token;
    this.user = user;
    this.user$.next(user);
    this.ls.setItem(this.JWT_TOKEN, token);
    this.ls.setItem(this.APP_USER, user);
    // sessionStorage.clear();
  }

  check(): Observable<boolean> {
    if (sessionStorage.getItem('user_id') != null || sessionStorage.getItem('user_id') === '') {
      return of(true);
    } else {
      return of(false);
    }
  }

  encodeData(data: any) {
    return btoa(data);
  }

  decodeData(data: any) {
    console.log("data decr",data);
    let user = atob(data);
    console.log("decr",user);
    return JSON.parse(user);
  }

  getEncryptedToken() {
    let user = this.getUser();
    let encodedUser = this.encodeData(user);
    return encodedUser;
  }
}
