import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LayoutComponent } from 'src/@npr/components/layout/layout.component';
import { AuthGuard } from './shared/guards/auth.guard';
import { CanDeactivateGuard } from './shared/guards/can-deactivate.guard';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'session/login',
    pathMatch: 'full'
  },
  {
    path: 'session',
    loadChildren: () => import('./views/auth/login/login.module').then(m => m.LoginModule),
  }, 
  {
    path:'forget-password',
    loadChildren: () => import('./views/forget-password/forget-password.module').then(m => m.ForgetPasswordModule),

  },{
    path: '',
    component: LayoutComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: 'dashboard',
        loadChildren: () => import('./views/dashboard/dashboard-analytics.module').then(m => m.DashboardAnalyticsModule),
      },{
        path: 'roles',
        loadChildren: () => import('./views/roles/roles.module').then(m => m.RolesModule),
      },{
        path: 'addSystem',
        loadChildren: () => import('./views/addSystem/add-system/add-system.module').then(m => m.AddSystemModule),
      },
      {
        path: 'addSystem/summary',
        loadChildren: () => import('./views/addSystem/add-system/add-system.module').then(m => m.AddSystemModule),
      },
      {
        path: 'audit-logs',
        loadChildren: () => import('./views/audit-logs/audit-logs.module').then(m => m.AuditLogsModule),
      },
      {
        path: 'change-password',
        loadChildren: () => import('./views/change-password/change-password.module').then(m => m.ChangePasswordModule),
      },
      {
        path: 'profile',
        loadChildren: () => import('./views/profile/profile.module').then(m => m.ProfileModule),
      },
      {
        path: 'process-monitoring',
        loadChildren: () => import('./views/process-monitoring/process-monitoring.module').then(m => m.ProcessMonitoringModule),
      },{
        path: 'security-policy',
        loadChildren: () => import('./views/security-policy/security-policy.module').then(m => m.SecurityPolicyModule),
      },
      // {
      //   path: 'summary',
      //   loadChildren: () => import('./views/security-policy/security-policy.module').then(m => m.SecurityPolicyModule),
      // },
      {
        path: 'createrule',
        loadChildren: () => import('./views/rule/rule.module').then(m => m.RuleModule),
      },{
        path: 'external-system',
        loadChildren: () => import('./views/file-upload-maintenance/file-upload-maintenance.module').then(m => m.FileUploadMaintenanceModule),
      },{
        path: 'system-param-maintenance',
        loadChildren: () => import('./views/system-param-maintenance/system-param-maintenance.module').then(m => m.SystemParamMaintenanceModule),
      },{
        path: 'file-upload',
        loadChildren: () => import('./views/file-upload/file-upload.module').then(m => m.FileUploadModule),
      },{
        path: 'process-account-closure',
        loadChildren: () => import('./views/process-account-closure/process-account-closure.module').then(m => m.ProcessAccountClosureModule),
      },{
        path: 'new-external-system',
        loadChildren: () => import('./views/new-external-system/new-external-system.module').then(m => m.NewExternalSystemModule),
      },{
        path: 'initiate-account-closure',
        loadChildren: () => import('./views/initiate-account-closure/initiate-account-closure.module').then(m => m.InitiateAccountClosureModule),
      },
      {
        path: 'initiate-account-closure/create',
        loadChildren: () => import('./views/initiate-account-closure/initiate-account-closure.module').then(m => m.InitiateAccountClosureModule),
      },{
        path: 'email-management',
        loadChildren: () => import('./views/email-management/email-management.module').then(m => m.EmailManagementModule),
      },
      {
        path: 'email-management/create',
        loadChildren: () => import('./views/email-management/email-management.module').then(m => m.EmailManagementModule),
      },{
        path: 'customer-search',
        loadChildren: () => import('./views/customer-search/customer-search.module').then(m => m.CustomerSearchModule),
      },
      {
        path: 'authorize-modification',
        loadChildren: () => import('./views/authorize-modification/authorize-modification.module').then(m => m.AuthorizeModificationModule),
      },{
        path: 'account-closure-maintenance',
        loadChildren: () => import('./views/account-closure-maintenance/account-closure-maintenance.module').then(m => m.AccountClosureMaintenanceModule),
      },
      {
        path: 'account-closure-maintenance/account-closure-summary',
        loadChildren: () => import('./views/account-closure-maintenance/account-closure-maintenance.module').then(m => m.AccountClosureMaintenanceModule),
      },{
        path: 'excel-mapping',
        loadChildren: () => import('./views/excel-mapping/excel-mapping.module').then(m => m.ExcelMappingModule),
      },{
        path: 'rule-mapping',
        loadChildren: () => import('./views/rule-mapping/rule-mapping.module').then(m => m.RuleMappingModule),
      },
      {
        path: 'charge-maintainence',
        loadChildren: () => import('./views/charge-maintainenece/charge-maintainenece.module').then(m => m.ChargeMaintaineneceModule),
      },
      {
        path: 'rule-translation',
        loadChildren: () => import('./views/rule-translation/rule-translation.module').then(m => m.RuleTranslationModule),
      },{
        path: 'users',
        loadChildren: () => import('./views/users/users.module').then(m => m.UsersModule),
      },
      {
        path: 'authorize-cash-payment',
        loadChildren: () => import('./views/authorize-cash-payment/authorize-cash-payments.module').then(m => m.AuthorizeCashPaymentsModule),
      },
      {
        path: 'sql-data',
        loadChildren: () => import('./views/banzpay/banzpay-sql-data.module').then(m => m.BanzpaySqlDataModule),
      },
      {
        path: 'amount-block',
        loadChildren: () => import('./views/amount-block/amount-block/amount-block.module').then(m => m.AmountBlockModule),
      },
      {
        path: 'accountBlocking',
        loadChildren: () => import('./views/account-blocking/account-block/account-block.module').then(m => m.AccountBlockModule),
      },
      {
        path: 'incoming',
        loadChildren: () => import('./views/incoming/incoming/incoming.module').then(m => m.IncomingModule),
      },
      {
        path: 'outgoing',
        loadChildren: () => import('./views/outgoing/outgoing/outgoing.module').then(m => m.OutgoingModule),
      },
    ]
  }, {
    path: '**',
    redirectTo: 'dashboard'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    // preloadingStrategy: PreloadAllModules,
    useHash: true,
    scrollPositionRestoration: 'enabled',
    relativeLinkResolution: 'corrected',
    anchorScrolling: 'enabled'
  })],
  providers: [CanDeactivateGuard],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
