import { Component, HostListener, Inject, Renderer2 } from "@angular/core";
import { DOCUMENT } from "@angular/common";
import { Platform } from "@angular/cdk/platform";
import { ActivatedRoute, Router } from "@angular/router";
import { Subject } from "rxjs";
import Swal from 'sweetalert2';

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"],
})

export class AppComponent {
  title = "Median";
  userActivity: any;
  userInactive: Subject<any> = new Subject();
  readonly SESSION_TIMEOUT_DURATION = 3 * 60 * 1000; // 15 minutes in milliseconds

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.document.body.classList.remove('activeNav');
  }

  constructor(
    private renderer: Renderer2,
    private platform: Platform,
    @Inject(DOCUMENT) private document: Document,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.changeTheme();
    this.setTimeout();
    this.userInactive.subscribe(() => this.logout());
  }

  setTimeout() {

    this.userActivity = setTimeout(
      () => this.userInactive.next(undefined),
      this.SESSION_TIMEOUT_DURATION
    );
  }

  logout() {
    if (localStorage.getItem("userFromLogin")) {
      Swal.fire("Session Expired", "Kindly Login to continue.");
    }
    localStorage.clear();
    sessionStorage.clear();
    window.location.reload();
    this.router.navigate(['/session/login']);
  }

  @HostListener("window:mousemove")
  @HostListener("window:click")
  @HostListener("window:keydown")
  refreshUserState() {
    clearTimeout(this.userActivity);
    this.setTimeout();
  }

  @HostListener("window:storage")
  onUpdateStorage() {
    const getOtpClicked = localStorage.getItem("getOtpClicked");
    if (getOtpClicked === 'true') {
      localStorage.clear();
      sessionStorage.clear();
      this.router.navigate(['/session/login']);
    }
  }

  changeTheme() {
    let lastBg = "";
    $(document).on('click', '.tColor', (e) => {
      e.preventDefault();
      const bgClass = $(this).data("id");
      $(this).addClass('active').parent().siblings().find('.tColor').removeClass('active');
      $('body').removeClass(lastBg).addClass(bgClass);
      lastBg = bgClass;
      $('.ddParent').removeClass('actDD');
    });
  }
}
